import React, { Fragement } from 'react';
import styled from 'styled-components';
import { Upload, Icon, message } from 'antd';
import axios from 'axios';

import UploadGuide from '../UploadGuide';
import HistoryTable from '../HistoryTable';

const requestUrl = 'https://skb89qsem5.execute-api.ap-northeast-2.amazonaws.com/dev/test/image';
const dummyBase64 = "";

const dummyData = {
  currentData: {
    jaw: 9.688786560366793,
    eye: 2.8912695962205643,
    lips: 25.463345061871614,
  },
  histories: [
    {
      jaw: 9.588786560366793,
      eye: 2.7912695962205643,
      lips: 23.463345061871614,
    },
    {
      jaw: 9.888786560366793,
      eye: 2.8732695962205643,
      lips: 24.463345061871614,
    },
  ],
};

function generateHistoryTableData(data) {
  console.log("Data")
  console.log(data)

  const initialData = Object.keys(data.currentData).length > 0 ? [{ key: 0, ...data.currentData }] : [];

  return data.histories.reduce((acc, history, index) => {
    acc.push({
      key: index + 1,
      ...history,
    });

    return acc;
  }, initialData);
}


function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener('load', () => callback(reader.result));
  reader.readAsDataURL(img);
}

function beforeUpload(file) {
  /*
  const isJPG = file.type === 'image/jpeg';
  if (!isJPG) {
    message.error('You can only upload JPG file!');
  }
  */
  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    message.error('Image must smaller than 2MB!');
  }
  return /* isJPG && */ isLt2M;
}

const Wrapper = styled.div`
`;

export default class DashboardPage extends React.PureComponent {
  state = {
    pristine: true,
    loading: false,
    error: false,
    data: {
      data : {
        currentData: {},
        histories: [],
      },
    },
  };

  uploadStart = base64 => {
    axios.post(requestUrl, {data : `"${base64.split(',')[1]}"`})
      .then(response => {
        this.setState({
          loading: false,
          data: response.data,
        });
      })
      .catch(error => {
        this.setState({
          loading: false,
          error: true,
          data: dummyData,
        });
        console.log(error);
      });
  }

  handleChange = (info) => {
    if (info.file.status === 'uploading') {
      this.setState({ pristine: false, loading: true, error: false });
      return;
    }
    if (info.file.status === 'done') {
      // Get this url from response in real world.
      getBase64(info.file.originFileObj, base64 => this.uploadStart(base64));
    }
  }

  render() {
    const {
      data,
      pristine,
      loading,
      error,
    } = this.state;

    const dataSource = generateHistoryTableData(data.data);

    return (
      <Wrapper>
        <UploadGuide />
        <Upload
          name="avatar"
          listType="picture-card"
          className="avatar-uploader"
          showUploadList={false}
          action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
          onChange={this.handleChange}
        >
          <div>
            <Icon type={loading ? 'loading' : 'plus'} />
            <div className="ant-upload-text">검사하기</div>
          </div>
        </Upload>
        <HistoryTable dataSource={dataSource} loading={loading} />
      </Wrapper>
    );
  }
}

